#topper-nav {
    background: $baseColor;
    color: #fff;
    padding: 8px; }

#first-section {
	min-height: 200px;
	overflow: hidden;

	[class^="col-md"] {
		padding: 0; }

	.container-fluid {
		padding: 0; }

	.row {
		margin: 0; }

	.dark-background {
		padding: 10px 30px; }

	.content {
		color: #fff;
		font-size: 16px;

		.row {
			margin-bottom: 20px; }

		.content-desc {
			font-weight: 300;

			.inner-title {
				font-weight: 400; }

			p {
				margin-bottom: 0; } } }

	.icon-bar {
		width: 100%;
		background: $yellow;
		// overflow: auto

		p {
			margin: 0; }

		a {
			border-bottom: 1px solid darken($yellow, 10%);
			display: block;
			text-align: center;
			padding: 16px;
			transition: all 0.3s ease;
			color: #333;

			&:hover {
				text-decoration: none;
				background-color: darken($yellow, 50%);
				color: #fff; } } } }
